





























import router from "@/router";
export default {
	name: "TopBar",
	methods: {
		navigate: function(target: string) {
			window.open(target, "_blank");
		},
	},
	data() {
		return {
			items: [
				{
					text: "Home",
					icon: "mdi-home",
					click: function() {
						router.push({ name: "Home" });
					},
				},
				{
					text: "Tools",
					icon: "mdi-hammer",
					click: function() {
						router.push({ name: "Tools" });
					},
				},
				{
					text: "Join",
					icon: "mdi-discord",
					click: function() {
						window.open("https://discord.gg/MU4XUVS", "_blank");
					},
				},
			],
		};
	},
};
