
























import Vue from "vue";
import { IEventLink } from "@/types/Event";

export default Vue.component("EventLink", {
	name: "EventLink",
	data: () => {
		return {
			showPicker: false,
			selectedEmoji: "",
		};
	},
	props: {
		eventLink: Object as () => IEventLink,
	},
	methods: {
		selectEmoji(emoji: any) {
			this.eventLink.reaction = emoji.data;
			this.showPicker = false;
		},
	},
});
