




















import Vue from "vue";
import TopBar from "./components/TopBar.vue";
export default Vue.extend({
	name: "App",
	components: { TopBar },
	methods: {
		navigate: function(target: string) {
			window.open(target, "_blank");
		},
	},
	data: () => ({}),
	computed: {
		theme() {
			return this.$vuetify.theme.dark ? "dark" : "light";
		},
	},
});
